@import '~antd/dist/antd.css';

.ant-layout-header {
    height: 32px;
    background-color: white;
}

.content-title {
    background: #ffb969;
    padding: 24px;
}

.content-main {
    font-size: 18px;
    max-width: 900px;
    margin: auto;
}

.headings {
    font-family: "Century Gothic", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}

.text-big {
    font-size: 5vmax;
}

.text-medium {
    font-size: 32px;
}

.text-miniMedium {
    font-size: 24px;
}

.myImg {
    height: auto;
    width: 100%;
    max-width: 300px;
    min-width: 100px;
}

.gradient-border {
    background-image: linear-gradient(to right, rgba(239, 210, 151, 0.95) 25px, transparent 85%);
    border: 0;
    height: 4px;
    margin: 6px 0 12px 0;
}

.gradient-border-end {
    background: rgb(234, 197, 91);
    height: 5px;
}

/** Animation styles can be found on https://github.com/kaustubhmenon/animation-library **/
/* fadeInDown */
@-webkit-keyframes fadeInDown {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(0, -20px, 0); }
    100% {
        opacity: 1;
        -webkit-transform: none; } }
@-moz-keyframes fadeInDown {
    0% {
        opacity: 0;
        -moz-transform: translate3d(0, -20px, 0); }
    100% {
        opacity: 1;
        -moz-transform: none; } }
@-o-keyframes fadeInDown {
    0% {
        opacity: 0;
        -o-transform: translate3d(0, -20px, 0); }
    100% {
        opacity: 1;
        -o-transform: none; } }
@keyframes fadeInDown {
    0% {
        opacity: 0;
        transform: translate3d(0, -20px, 0); }
    100% {
        opacity: 1;
        transform: none; } }

.u--fadeInDown {
    -webkit-animation: fadeInDown 1s ease;
    -moz-animation: fadeInDown 1s ease;
    -o-animation: fadeInDown 1s ease;
    animation: fadeInDown 1s ease; }

